<script>
    import './index.less';

    export default{
        data () {
            return {
                username: '',
                disabled: false,
                packages: [1],
                finded: false,
                baseUrl: 'https://www.npmjs.com/package/',
                terminal: null
            };
        },

        methods: {
            handleValChange () {
                console.log('change');
            },

            getUserPackages (username) {
                return new Promise((resolve, reject) => {
                    fetch(`http://115.28.240.235/stars?username=${username}`).then(response => {
                        return response.json();
                    }).then((res) => {
                        if (res.code > 0) {
                            return resolve([res.error]);
                        }
                        if (!res.pkgs[0]) {
                            resolve(['npm WARN stars user has not starred any packages']);
                        } else {
                            resolve(res.pkgs);
                        }
                    }, (e) => {
                        resolve(['Maybe some error happened']);
                    });
                });
            }
        },

        mounted () {
            if (!window.Promise || !window.fetch) {
                console.error('Your browser does not support Promise or Fetch.\n See supported browsers: https://caniuse.com');
            }

            this.$nextTick(() => {
                this.terminal = new Terminal('terminal', {}, {
                    execute: (cmd, args) => {
                        switch (cmd) {
                            case 'clear':
                                this.terminal.clear();
                                return '';
                            case 'help':
                                return 'Commands: clear, help, theme, ver or version';
                            case 'theme': 
                                const curTheme = this.terminal.getTheme();
                                if (args && args[0]) {
                                    const param = args[0];
                                    if (param === 'help') {
                                        return 'Alternative theme: interlaced, modern or white. run theme &lt;theme-name&gt; to change the theme';
                                    } else if (param.match(/^interlaced|modern|white$/)) {
                                        this.terminal.setTheme(args[0]); 
                                        return '';
                                    } else {
                                        return 'Invalid theme name';
                                    }
                                } else {
                                    return `Run theme help to find alternative theme, and current theme is ${curTheme}`;
                                }
                            case 'ver':
                            case 'version':
                                return '1.0.1';
                            case 'npm': 
                                if (args && args.length === 2 && args[0] === 'stars') {
                                    return this.getUserPackages(args[1]);
                                } else {
                                    return 'npm stars &lt;username&gt;';
                                }
                            default:
                                return false;
                        } 
                    }
                });
            });
        }
    };
</script>
